@import "settings";

:root { font-size: 10px; }
html{
  font-size: 1.0rem;
}

html,body{
  height: 100%;
  background-image: url('./images/one_pager.jpg');
  color: white;
  @include quickSand;
}

h1,h2,h3,h4{
  @include quickSand;
  font-size: 4.2rem;
  font-weight: 700;
}

.negative_margin{
  margin: 0 -15px;
}

.draw_content_container{
  width: 100%;
  max-width: 97.9rem;
  margin: 0 auto;
}

.custom-button{
  @include btn;
}

span.large_text{
  font-size: 3rem;
  font-weight: 700;
  margin: 0 0.7rem;
}



